import { NavLink } from "react-router-dom"

const NavBar = () => {
    return (
        <header className="font-title bg-red-400">
            <div className="container mx-auto flex justify-between">
                <nav className="flex">
                    <NavLink 
                        to="/" 
                        exact 
                        activeClassName="text-white"
                        className="inline-flex items-center py-6 px-3 mr-4 text-red-100 hover:text-white text-4xl font-bold tracking-widest"
                    >
                        Stallout
                    </NavLink>
                    <NavLink 
                        to="/games"
                        activeClassName="text-white"
                        className="inline-flex items-center py-3 px-3 mr-6 rounded text-red-200 hover:text-white text-xl font-bold tracking-widest"
                    >
                        Games
                    </NavLink>
                    <NavLink 
                        to="/concept" 
                        activeClassName="text-white"
                        className="inline-flex items-center py-3 px-3 mr-6 rounded text-red-200 hover:text-white text-xl font-bold tracking-widest"
                    >
                        Concept Art
                    </NavLink>
                    <NavLink 
                        to="/illustrations" 
                        activeClassName="text-white"
                        className="inline-flex items-center py-3 px-3 mr-6 rounded text-red-200 hover:text-white text-xl font-bold tracking-widest"
                    >
                        Illustrations
                    </NavLink>
                    <NavLink 
                        to="/ui"
                        activeClassName="text-white" 
                        className="inline-flex items-center py-3 px-3 mr-6 rounded text-red-200 hover:text-white text-xl font-bold tracking-widest"
                    >
                        UI
                    </NavLink>
                </nav>
            </div>
        </header>
    )
}

export default NavBar
